import { Component, Input } from '@angular/core'
import { environment } from 'src/environments/environment'
import { ICard } from '../api-interfaces'

@Component({
    selector: 'card-dummy',
    templateUrl: 'card-dummy.component.html',
})
export class CardDummyComponent {
    @Input()
    public card: Partial<ICard>
    @Input()
    public disabled: boolean
    @Input()
    public backgroundImage: string
    @Input()
    public isLoading: boolean

    public getLogo(): string {
        switch (environment.appName) {
            case 'sgpmx':
                return '/img/brand/logo-symbol.svg'
            case 'premiertrust':
            case 'ubk':
            case 'birex':
            case 'usunpay':
            case 'etransfer':
                return '/img/brand/logo.svg'
            default:
                return '/img/brand/logo-symbol-white.svg'
        }
    }

    public getBackgroundImageStyle() {
        if (!this.backgroundImage) {
            return
        }
        const styles = {
            backgroundImage: 'url(' + this.backgroundImage + ')',
            height: '190px',
            width: '300px',
            backgroundSize: 'contain',
            zIndex: 'auto',
            border: 'none',
            boxShadow: 'none',
        }
        return styles
    }
}
