import { HttpClient } from '@angular/common/http'
import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ModalController } from '@ionic/angular'
import BigNumber from 'bignumber.js'
import { forkJoin, of, Subject, Subscription } from 'rxjs'
import { finalize, map, switchMap, take, tap } from 'rxjs/operators'
import { CurrenciesQuery } from 'src/app/store/currencies/currencies.query'
import { ICard, ICurrency, IUser } from '../../api-interfaces'
import { Fee } from '../../models/accounting/fee.model'
import { FeeService } from '../../services/accounting/fee.service'
import { SessionService } from '../../services/session.service'
import { GetExchangeRateResponse } from '../modal-topup/modal-topup.page'

interface NiumRefundWalletResponse {
    message: string
    systemReferenceNumber: string
}

type NiumCurrency = 'AUD' | 'CAD' | 'EUR' | 'GBP' | 'HKD' | 'JPY' | 'SGD' | 'USD'

@Component({
    selector: 'app-modal-unload',
    templateUrl: './modal-unload.page.html',
    styleUrls: ['../card-view-modal.scss'],
})
export class ModalUnloadPage implements OnInit, OnDestroy {
    @Input()
    public card: ICard
    @Input()
    public balances: any[]

    public sourceCurrencyCode: NiumCurrency = 'USD'
    public destinationCurrency: ICurrency
    public amount: string

    public exchangeRate = '1'
    public unloadCardFee: Fee | null
    public niumCurrencies: NiumCurrency[] = ['AUD', 'CAD', 'EUR', 'GBP', 'HKD', 'JPY', 'SGD', 'USD']
    public isLoading = false
    public isFetching = false

    @ViewChild('unloadForm')
    public unloadForm: NgForm

    public fetchEvent = new Subject<void>()
    private subscription = new Subscription()

    constructor(
        private http: HttpClient,
        public session: SessionService,
        private modalController: ModalController,
        private feeService: FeeService,
        private currenciesQuery: CurrenciesQuery
    ) {}

    public ngOnInit() {
        this.currenciesQuery
            .selectEntity('USD')
            .pipe(take(1))
            .subscribe(usd => {
                if (usd) {
                    this.destinationCurrency = usd
                }
            })
        this.subscription.add(
            this.fetchEvent
                .pipe(
                    tap(() => {
                        this.isFetching = true
                    }),
                    switchMap(() =>
                        forkJoin([
                            this.feeService.fetch({
                                type: 'deposit',
                                method: 'card',
                                currency: { code: this.destinationCurrency.code } as ICurrency,
                                user: { id: this.session.user.id } as IUser,
                            }),
                            this.sourceCurrencyCode !== this.destinationCurrency.code
                                ? this.http
                                      .post<GetExchangeRateResponse>(
                                          `/card-programs/${this.card.cardholder?.cardProgram?.id}/get-exchange-rate`,
                                          {
                                              destinationCurrencyCode: this.destinationCurrency.code,
                                              sourceCurrencyCode: this.sourceCurrencyCode,
                                          }
                                      )
                                      .pipe(map(response => response.exchangeRate))
                                : of('1'),
                        ]).pipe(
                            finalize(() => {
                                this.isFetching = false
                            })
                        )
                    )
                )
                .subscribe(([fee, exchangeRate]) => {
                    this.unloadCardFee = fee
                    this.exchangeRate = exchangeRate
                })
        )
        this.fetchEvent.next()
    }

    public ngOnDestroy() {
        this.subscription.unsubscribe()
    }

    public async submit(): Promise<void> {
        if (this.unloadForm.invalid) {
            return
        }
        this.isLoading = true
        this.http
            .post<NiumRefundWalletResponse>(`/cards/${this.card.id}/refund`, {
                sourceCurrencyCode: this.sourceCurrencyCode,
                amount: this.amount + '',
                destinationCurrencyCode: this.destinationCurrency.code,
            })
            .pipe(
                finalize(() => {
                    this.isLoading = false
                })
            )
            .subscribe(response => {
                this.modalController.dismiss(response)
            })
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }

    public getCardBalance(code: string): string {
        const found = this.balances.find(b => b.curSymbol === code)
        if (found) {
            return found.balance + ''
        }
        return '0.00'
    }

    public calculateFee(): string {
        let relativeFeeAmount = new BigNumber(0)
        if (this.unloadCardFee?.relative) {
            relativeFeeAmount = new BigNumber(this.amount || 0)
                .times(this.exchangeRate)
                .times(this.unloadCardFee.relative || 0)

            if (this.unloadCardFee.min && relativeFeeAmount.isLessThan(this.unloadCardFee.min)) {
                relativeFeeAmount = new BigNumber(this.unloadCardFee.min)
            } else if (this.unloadCardFee.max && relativeFeeAmount.isGreaterThan(this.unloadCardFee.max)) {
                relativeFeeAmount = new BigNumber(this.unloadCardFee.max)
            }
        }
        return new BigNumber(this.unloadCardFee?.fixed || 0).plus(relativeFeeAmount).toFixed(2, 1)
    }

    public calculateAmount(): string {
        if (!this.amount) {
            return '0'
        }
        return new BigNumber(this.amount).times(this.exchangeRate).minus(this.calculateFee()).toFixed(2, 1)
    }
}
