import { HttpClient } from '@angular/common/http'
import { Component, OnDestroy, OnInit } from '@angular/core'
import { faEye } from '@fortawesome/pro-solid-svg-icons'
import { AlertController, ModalController } from '@ionic/angular'
import { NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import BigNumber from 'bignumber.js'
import { parseISO } from 'date-fns'
import { cloneDeep } from 'lodash'
import { merge, Observable, of, Subject, Subscription } from 'rxjs'
import { finalize, map, switchMap, tap, withLatestFrom } from 'rxjs/operators'
import { ICard, ICurrency, Paginated, Transaction, UserDetails, Wallet } from 'src/app/common/api-interfaces'
import { Currency } from 'src/app/common/models/accounting/currency.model'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { CurrenciesQuery } from 'src/app/store/currencies/currencies.query'
import { WalletsQuery } from 'src/app/store/wallets/wallets.query'
import { WalletsService } from 'src/app/store/wallets/wallets.service'
import { CardOrderTermsComponent } from 'src/app/user-cards/card-order/card-order-terms/card-order-terms.component'
import { environment } from 'src/environments/environment'
import { buildFileUrl } from '../file'
import { SecurityCheckService } from '../security-check/security-check.service'
import { SessionService } from '../services/session.service'
import { TransactionService } from '../services/transaction.service'
import { CardDetailsPage } from './card-details/card-details.page'
import { ModalChangePinPage } from './modal-change-pin/modal-change-pin.page'
import { ModalDownloadTransactionsPage } from './modal-download-transactions/modal-download-transactions.page'
import { ModalFeesPage } from './modal-fees/modal-fees.page'
import { ModalOffledgerPage } from './modal-offledger/modal-offledger.page'
import { ModalSwapPage } from './modal-swap/modal-swap.page'
import { ModalTopupPage } from './modal-topup/modal-topup.page'
import { ModalUnloadPage } from './modal-unload/modal-unload.page'

@Component({
    selector: 'app-card-view',
    templateUrl: './card-view.page.html',
    styleUrls: ['./card-view.page.scss'],
})
export class CardViewPage implements OnInit, OnDestroy {
    public activeTab: 'history' | 'topup' = 'history'
    public transaction: Transaction = {
        type: 'withdrawal',
        method: 'card',
        counterWallet: {
            user: null,
        },
    } as Transaction
    public item: ICard
    public userDetails: UserDetails
    public copy: Partial<ICard> = {}
    public currencies: Currency[]
    public balances$: Observable<any[]>
    public balances: any[]
    public transactions: any[] = []
    public page = 1
    public size = 5
    public totalElements: number

    public isFetching = false
    public isBlockUnblockLoading = false
    public faEye = faEye
    public digitalCardArtWorkUrl: string | null

    public fetchBalanceEvent = new Subject<void>()
    public wallets$: Observable<(Wallet & { value: string })[]>
    public appName = environment.appName

    private subscriptions = new Subscription()

    constructor(
        public session: SessionService,
        private http: HttpClient,
        private toastr: ToastrService,
        private walletsQuery: WalletsQuery,
        private modalController: ModalController,
        public alertController: AlertController,
        private securityCheckService: SecurityCheckService,
        private ngbModal: NgbModal,
        private currenciesQuery: CurrenciesQuery,
        private walletsService: WalletsService,
        private transactionService: TransactionService,
        public translate: TranslateService
    ) {
        this.wallets$ = this.walletsQuery.filteredBalances$
    }

    public ngOnInit(): void {
        this.http.get<ICard>(`/cards/${this.item.id}`).subscribe(card => {
            if (card) {
                Object.assign(this.item, card)
                this.copy = cloneDeep(this.item)
                this.transaction.card = card

                if (card.cardholder?.cardProgram?.digitalCardArtwork) {
                    this.digitalCardArtWorkUrl = buildFileUrl(card.cardholder.cardProgram.digitalCardArtwork)
                }
            }
        })

        this.http
            .get<Paginated<ICurrency>>('/currencies', { params: { limit: 100 + '' } })
            .pipe(map(response => response.data.map(payload => new Currency(payload))))
            .subscribe(currencies => {
                this.currencies = currencies
            })

        this.http.get<UserDetails>(`/users/${this.item.cardholder?.id}/details`).subscribe(details => {
            this.userDetails = details
        })

        this.balances$ = merge(of(undefined), this.fetchBalanceEvent).pipe(
            switchMap(() => this.http.get<any[]>(`/cards/${this.item.id}/balance`)),
            map(balance => balance.filter(currency => currency.balance !== 0)),
            map(balance => (balance.length ? balance : [{ curSymbol: 'USD', balance: 0 }])),
            withLatestFrom(this.currenciesQuery.selectAll()),
            map(([balance, availableCurrencies]) =>
                balance.map(b => {
                    Object.assign(b, {
                        balanceInUsd: new BigNumber(b.balance)
                            .times(
                                availableCurrencies.find(currency => currency.code === b.curSymbol)?.exchangeRate || 1
                            )
                            .toNumber(),
                    })

                    return b
                })
            ),
            tap(balance => {
                this.balances = balance.sort((prev: any, next: any) => next.balanceInUsd - prev.balanceInUsd)
            })
        )

        this.copy = cloneDeep(this.item)
        this.fetchTransactions()
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }

    public fetchTransactions(event?: any, reset?: boolean): void {
        this.isFetching = true
        if (reset) {
            this.page = 1
        }

        this.http
            .get<any>(`/cards/${this.item.id}/transactions`, {
                params: { page: this.page - 1 + '', size: this.size + '' },
            })
            .subscribe(response => {
                this.isFetching = false
                if (response.content) {
                    const transactions = response.content.map((content: any) => ({
                        ...content,
                        createdAt: parseISO(content.createdAt),
                    }))
                    this.totalElements = response.totalElements || '0'
                    this.transactions = reset ? transactions : this.transactions.concat(transactions)
                    this.page += 1
                }
                if (event) {
                    event.target.complete()
                }
            })
    }

    public getCurrencyByCode(code: string): Currency | undefined {
        return this.currencies.find(currency => currency.code === code)
    }

    public async blockUnblockCard(checked: boolean): Promise<void> {
        const alert = await this.alertController.create({
            header: checked
                ? this.translate.instant('common.block-your-card')
                : this.translate.instant('common.unblock-your-card'),
            message: this.translate.instant('common.you-can-revert'),
            buttons: [
                {
                    text: this.translate.instant('common.cancel'),
                    role: 'cancel',
                },
                {
                    text: checked ? this.translate.instant('common.block') : this.translate.instant('common.unblock'),
                    cssClass: 'text-danger',
                    handler: async () => {
                        this.isBlockUnblockLoading = true
                        this.http
                            .put<ICard>(`/cards/${this.item.id}/status`, {
                                status: checked ? 'block' : 'unblock',
                            })
                            .pipe(
                                finalize(() => {
                                    this.isBlockUnblockLoading = false
                                })
                            )
                            .subscribe(card => {
                                Object.assign(this.item, card)
                                this.copy = cloneDeep(this.item)
                                this.transaction.card = card
                                this.toastr.success(
                                    `${this.translate.instant('common.card')} ${
                                        checked
                                            ? this.translate.instant('common.block')
                                            : this.translate.instant('common.unblock')
                                    }`
                                )
                            })
                    },
                },
            ],
        })
        await alert.present()
    }

    public async presentTopupModal(): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalTopupPage,
            cssClass: 'modal-container',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                item: this.item,
                balances: this.balances,
            },
        })
        await modal.present()
        modal.onWillDismiss().then(async ({ data }) => {
            this.fetchTransactions(null, true)
            this.fetchBalanceEvent.next()

            if (!data) {
                return
            }
            const alert = await this.alertController.create({
                header: this.translate.instant('common.topup-completed'),
                message: `${this.translate.instant(
                    'common.you-successfully-topped-up'
                )} ${data.destinationAmount.toFixed(2)} ${data.destinationCurrencyCode}.`,
                buttons: ['OK'],
            })
            await alert.present()
            this.refreshBalance()
        })
    }

    public async openChangePinModal(): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalChangePinPage,
            cssClass: 'modal-change-pin',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                item: this.item,
            },
        })
        await modal.present()
        modal.onWillDismiss().then(async ({ data }) => {
            if (!data) {
                return
            }
            const alert = await this.alertController.create({
                header: this.translate.instant('common.pin-updated'),
                message: this.translate.instant('common.you-successfully-update-card-pin'),
                buttons: ['OK'],
            })
            await alert.present()
        })
    }

    public async openFeesModal(): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalFeesPage,
            cssClass: 'modal-fees',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                item: this.item,
            },
        })
        await modal.present()
    }

    public async openDownloadReportModal(): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalDownloadTransactionsPage,
            cssClass: 'modal-download-report',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                item: this.item,
            },
        })
        await modal.present()
    }

    public async presentCardDetails(): Promise<void> {
        if (this.item.type !== 'virtual') {
            return
        }
        this.subscriptions.add(
            this.securityCheckService.getScopePermission('banking').subscribe(async () => {
                const modal = await this.modalController.create({
                    component: CardDetailsPage,
                    cssClass: 'modal-card',
                    presentingElement: await this.modalController.getTop(),
                    componentProps: {
                        item: this.item,
                        backgroundImage: this.digitalCardArtWorkUrl,
                    },
                })
                await modal.present()
            })
        )
    }

    public openViewNiumTermsModal(): void {
        const termsModal = this.ngbModal.open(CardOrderTermsComponent, {
            windowClass: 'modal-primary view-terms-modal',
            backdrop: 'static',
            backdropClass: 'view-terms-modal',
        })
        termsModal.componentInstance.viewOnly = true
    }

    public async offLedgerModal(type: string): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalOffledgerPage,
            cssClass: 'modal-container',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                item: this.item,
                balances: this.balances,
                type,
            },
        })
        await modal.present()
        modal.onWillDismiss().then(async ({ data }) => {
            if (!data) {
                return
            }
            this.fetchTransactions(null, true)
            this.fetchBalanceEvent.next()
            let dismissHeader
            let dismissMessage
            if (type === 'load') {
                dismissHeader = this.translate.instant('common.topup-completed')
                dismissMessage = `this.translate.instant(
                    'common.you-successfully-topped-up'
                ) (off ledger) ${data.destinationAmount.toFixed(2)} ${data.destinationCurrencyCode}.`
            } else {
                dismissHeader = this.translate.instant('common.unload-card-completed')
                dismissMessage = `${this.translate.instant('common.unload-card')} ${data.message}`
            }

            if (!data) {
                return
            }
            const alert = await this.alertController.create({
                header: dismissHeader,
                message: dismissMessage,
                buttons: ['OK'],
            })
            await alert.present()
        })
    }

    public async openUnloadModal(): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalUnloadPage,
            cssClass: 'modal-container',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                card: this.item,
                balances: this.balances,
            },
        })
        await modal.present()
        modal.onWillDismiss().then(async ({ data }) => {
            this.fetchTransactions(null, true)
            this.fetchBalanceEvent.next()

            if (!data) {
                return
            }
            const alert = await this.alertController.create({
                header: this.translate.instant('common.unload-card-completed'),
                message: `${this.translate.instant('common.unload-card')} ${data.message}`,
                buttons: ['OK'],
            })
            await alert.present()
            this.refreshBalance()
        })
    }

    public async openSwapModal(): Promise<void> {
        const modal = await this.modalController.create({
            component: ModalSwapPage,
            cssClass: 'modal-container',
            presentingElement: await this.modalController.getTop(),
            componentProps: {
                item: this.item,
                balances: this.balances,
            },
        })
        await modal.present()
        modal.onWillDismiss().then(async ({ data }) => {
            this.fetchTransactions(null, true)
            this.fetchBalanceEvent.next()

            if (!data) {
                return
            }
            console.log(data)
            const alert = await this.alertController.create({
                header: this.translate.instant('common.exchange-completed'),
                message: `${data.destinationAmount} ${data.destinationCurrencyCode} ${this.translate.instant(
                    'common.has-been-credited'
                )}`,
                buttons: ['OK'],
            })
            await alert.present()
            this.refreshBalance()
        })
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }

    public formatPan(pan: string): string {
        return `${pan.slice(0, 4)} ${pan.slice(4, 8)} ${pan.slice(8, 12)} ${pan.slice(12)}`
    }

    public getDescription(tx: any): string {
        if (tx.merchantName) {
            switch (tx.transactionType) {
                case 'Auto_Sweep':
                    return `<b>${this.translate.instant('card-view.auto-sweep')}</b>: ` + tx.merchantName
                case 'Fee_Debit':
                    let label = ''
                    switch (tx.labels.feeName) {
                        case 'INTERNATIONAL_ATM_FEE':
                        case 'ATM_FEE':
                            label = 'ATM Fee'
                            break
                        case 'TRANSACTION_MARKUP':
                            label = `${this.translate.instant('card-view.fx-fee')}`
                            break
                        case 'ATM_DECLINE_FEE':
                            label = 'ATM Decline Fee'
                            break
                        case 'NON_ATM_DECLINE_FEE':
                            label = this.translate.instant('common.decline-fee')
                            break
                        case 'ECOM_FEE':
                            label = `${this.translate.instant('card-view.ecommerce-fee')}`
                            break
                    }
                    return label + ': ' + tx.merchantName
                default:
                    return tx.merchantName
            }
        }
        switch (tx.transactionType) {
            case 'Fee_Debit':
            case 'Fee_Waiver':
                let label = ''
                switch (tx.labels.feeName) {
                    case 'ACCOUNT_MAINTENANCE_FEE':
                        label = `${this.translate.instant('card-view.maintenance-fee')}`
                        break
                    case 'ACCOUNT_INACTIVE_FEE':
                        label = `${this.translate.instant('card-view.inactivity-fee')}`
                        break
                }
                return label
        }
        if (tx.transactionType.includes('Wallet_Credit_Mode_Prefund')) {
            return `${this.translate.instant('card-view.topup-from-wallet')}`
        }
        if (tx.transactionType.includes('Wallet_Refund')) {
            return `${this.translate.instant('card-view.unload-from-card')}`
        }
        if (tx.transactionType.includes('Wallet_Fund_Transfer')) {
            return `${this.translate.instant('common.currency-exchange')}`
        }
        return tx.transactionType
    }

    public getTransactionColor(tx: any): string {
        if (tx.status === 'Declined') {
            return 'medium'
        }
        switch (tx.transactionType) {
            case 'Auto_Sweep':
                return 'primary'
            case 'Fee_Debit':
                return 'danger-alt'
            case 'Wallet_Fund_Transfer':
                return 'primary'
            default:
                return tx.debit ? 'danger' : 'success'
        }
    }

    public getTransactionIcon(tx: any): string {
        switch (tx.transactionType) {
            case 'Auto_Sweep':
                return 'swap-horizontal'
            case 'Fee_Debit':
                return 'arrow-down-circle'
            case 'Wallet_Fund_Transfer':
                return 'swap-horizontal-outline'
            default:
                return tx.debit ? 'arrow-down' : 'arrow-up'
        }
    }

    private refreshBalance(): void {
        this.walletsService.fetch()
        this.transactionService.changeEvent.emit()
    }
}
