import { HttpClient } from '@angular/common/http'
import { Component, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ModalController } from '@ionic/angular'
import { finalize } from 'rxjs/operators'
import { ICard } from '../../api-interfaces'
import { SessionService } from '../../services/session.service'

@Component({
    selector: 'app-modal-change-pin',
    templateUrl: './modal-change-pin.page.html',
    styleUrls: ['./modal-change-pin.page.scss', '../card-view-modal.scss'],
})
export class ModalChangePinPage implements OnInit {
    public item: ICard
    public pinBlock = ''
    public repeatedPin: string
    public isLoading = false

    @ViewChild('changePinForm')
    public changePinForm: NgForm

    constructor(private http: HttpClient, public session: SessionService, private modalController: ModalController) {}

    ngOnInit() {}

    public async changePin(): Promise<void> {
        if (this.changePinForm.invalid) {
            return
        }
        this.isLoading = true
        this.http
            .put<ICard>(`/cards/${this.item.id}/pin`, { pin: this.pinBlock })
            .pipe(
                finalize(() => {
                    this.isLoading = false
                })
            )
            .subscribe(response => {
                this.modalController.dismiss(response)
            })
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }
}
