import { HttpClient } from '@angular/common/http'
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ModalController } from '@ionic/angular'
import BigNumber from 'bignumber.js'
import { Subscription } from 'rxjs'
import { finalize } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { ICard, ICurrency } from '../../api-interfaces'
import { Fee } from '../../models/accounting/fee.model'
import { FeeService } from '../../services/accounting/fee.service'
import { SessionService } from '../../services/session.service'

interface SwapPayload {
    sourceCurrencyCode: string
    amount: number | string
    destinationCurrencyCode: string
}

interface NiumFundWalletResponse {
    systemReferenceNumber: string
    sourceAmount: number
    sourceCurrencyCode: string
    destinationAmount: number
    destinationCurrencyCode: string
    status: 'Pending' | 'Approved' | 'Declined'
    paymentMethods: string[]
}

export interface GetExchangeRateResponse {
    sourceCurrencyCode: string
    sourceAmount?: string
    destinationCurrencyCode: string
    destinationAmount?: string
    exchangeRate: string
}

@Component({
    selector: 'app-modal-swap',
    templateUrl: './modal-swap.page.html',
})
export class ModalSwapPage implements OnInit, OnDestroy {
    public item: ICard
    public balances: any[]

    public swap: SwapPayload = {
        sourceCurrencyCode: '',
        amount: '',
        destinationCurrencyCode: '',
    }
    public isLoading = false
    public isGettingExchangeRate = false
    public cardTopupFee: Fee | null
    public exchangeRate = '1'

    public niumCurrencies = ['AUD', 'CAD', 'EUR', 'GBP', 'HKD', 'JPY', 'SGD', 'USD']
    public filteredCardBalances: Record<string, any>[]
    public filteredDestinationCurrencies: string[]

    public appName = environment.appName
    public currency: ICurrency

    public subcriptions = new Subscription()

    @ViewChild('transactionForm')
    public transactionForm: NgForm

    constructor(
        private http: HttpClient,
        public session: SessionService,
        private modalController: ModalController,
        private feeService: FeeService
    ) {}

    public ngOnInit() {
        this.filterSourceCurrencies()
        this.swap.sourceCurrencyCode = 'USD'
        this.filterDestinationCurrencies()
        this.swap.destinationCurrencyCode = this.filteredDestinationCurrencies[0]
        this.getExchangeRate()
    }

    public ngOnDestroy() {
        this.subcriptions.unsubscribe()
    }

    public async topupCard(): Promise<void> {
        if (this.transactionForm.invalid) {
            return
        }
        this.isLoading = true

        this.swap.amount = this.swap.amount + ''

        this.http
            .post<NiumFundWalletResponse>(`/cards/${this.item.id}/swap`, this.swap)
            .pipe(
                finalize(() => {
                    this.isLoading = false
                })
            )
            .subscribe(response => {
                this.modalController.dismiss(response)
            })
    }

    public filterSourceCurrencies() {
        this.filteredCardBalances = this.balances.filter(b => !new BigNumber(b.balance).isZero())
    }

    public filterDestinationCurrencies() {
        this.filteredDestinationCurrencies = this.niumCurrencies.filter(c => c !== this.swap.sourceCurrencyCode)
    }

    public getCardBalance(code: string): string {
        const found = this.balances.find(b => b.curSymbol === code)
        if (found) {
            return found.balance + ''
        }
        return '0.00'
    }

    public useMax(): void {
        const found = this.balances.find(balance => balance.curSymbol === this.swap.sourceCurrencyCode)
        if (found) {
            this.swap.amount = parseFloat(found.balance)
        }
    }

    public calculateFee(): string {
        const { relativeFee, fixedFee } = this.feeService.determineFee(
            {
                amount: this.swap.amount + '' || '0',
                currency: this.currency,
            },
            this.cardTopupFee
        )
        const relativeFeeAmount = new BigNumber(this.swap.amount || 0).times(relativeFee || 0)
        const totalFee = new BigNumber(fixedFee || 0).plus(relativeFeeAmount)

        return totalFee.toFixed(2, 1)
    }

    public isForexTopup(): boolean {
        return this.swap.sourceCurrencyCode !== this.swap.destinationCurrencyCode
    }

    public getExchangeRate(): void {
        this.isGettingExchangeRate = true
        if (this.swap.sourceCurrencyCode !== this.swap.destinationCurrencyCode) {
            this.http
                .post<GetExchangeRateResponse>(
                    `/card-programs/${this.item.cardholder?.cardProgram?.id}/get-exchange-rate`,
                    {
                        sourceCurrencyCode: this.swap.sourceCurrencyCode,
                        destinationCurrencyCode: this.swap.destinationCurrencyCode,
                    }
                )
                .pipe(
                    finalize(() => {
                        this.isGettingExchangeRate = false
                    })
                )
                .subscribe(response => {
                    this.exchangeRate = response.exchangeRate
                })
        } else {
            this.exchangeRate = '1'
            this.isGettingExchangeRate = false
        }
    }

    public calculateAmount(): string {
        if (!this.swap.amount) {
            return '0'
        }

        return new BigNumber(this.swap.amount || 0)
            .minus(new BigNumber(this.calculateFee()))
            .times(this.exchangeRate)
            .toFixed(2)
    }

    public compareCurrencies(a: any, b: string): boolean {
        return a.curSymbol === b
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }
}
