import { HttpClient } from '@angular/common/http'
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ModalController } from '@ionic/angular'
import BigNumber from 'bignumber.js'
import { Subscription } from 'rxjs'
import { finalize, take } from 'rxjs/operators'
import { CurrenciesQuery } from 'src/app/store/currencies/currencies.query'
import { WalletsQuery } from 'src/app/store/wallets/wallets.query'
import { environment } from 'src/environments/environment'
import { ICard, ICurrency } from '../../api-interfaces'
import { Fee } from '../../models/accounting/fee.model'
import { User } from '../../models/core/user.model'
import { FeeService } from '../../services/accounting/fee.service'
import { SessionService } from '../../services/session.service'

interface TopupPayload {
    sourceCurrencyCode: string
    amount: number | string
    destinationCurrencyCode: string
}

interface NiumFundWalletResponse {
    systemReferenceNumber: string
    sourceAmount: number
    sourceCurrencyCode: string
    destinationAmount: number
    destinationCurrencyCode: string
    status: 'Pending' | 'Approved' | 'Declined'
    paymentMethods: string[]
}

export interface GetExchangeRateResponse {
    sourceCurrencyCode: string
    sourceAmount?: string
    destinationCurrencyCode: string
    destinationAmount?: string
    exchangeRate: string
}

@Component({
    selector: 'app-modal-topup',
    templateUrl: './modal-topup.page.html',
    styleUrls: ['../card-view-modal.scss'],
})
export class ModalTopupPage implements OnInit, OnDestroy {
    public item: ICard
    public balances: any[]

    public from: ICurrency = { code: 'USD' } as any
    public topup: TopupPayload = {
        sourceCurrencyCode: 'USD',
        amount: '',
        destinationCurrencyCode: 'USD',
    }
    public isLoading = false
    public isGettingExchangeRate = false
    public cardTopupFee: Fee | null
    public exchangeRate = '1'

    public niumCurrencies = ['AUD', 'CAD', 'EUR', 'GBP', 'HKD', 'JPY', 'SGD', 'USD']

    public appName = environment.appName
    public currency: ICurrency

    public subcriptions = new Subscription()

    @ViewChild('transactionForm')
    public transactionForm: NgForm

    constructor(
        private http: HttpClient,
        public session: SessionService,
        private modalController: ModalController,
        private walletsQuery: WalletsQuery,
        private currenciesQuery: CurrenciesQuery,
        private feeService: FeeService
    ) {}

    public ngOnInit() {
        this.subcriptions.add(
            this.feeService
                .fetch({
                    type: 'withdrawal',
                    method: 'card',
                    currency: this.from,
                    user: new User(this.session.user),
                })
                .subscribe(cardTopupFee => (this.cardTopupFee = cardTopupFee))
        )
        this.subcriptions.add(
            this.currenciesQuery
                .selectEntity('USD')
                .pipe(take(1))
                .subscribe(usd => {
                    this.currency = usd!
                })
        )

        if (this.niumCurrencies.includes(this.session.user.preferredCurrency.code)) {
            this.topup.destinationCurrencyCode = this.session.user.preferredCurrency.code
        }
    }

    public ngOnDestroy() {
        this.subcriptions.unsubscribe()
    }

    public async topupCard(): Promise<void> {
        if (this.transactionForm.invalid) {
            return
        }
        this.isLoading = true
        this.topup.sourceCurrencyCode = this.from.code
        this.topup.amount += ''
        this.http
            .post<NiumFundWalletResponse>(`/cards/${this.item.id}/topup`, this.topup)
            .pipe(
                finalize(() => {
                    this.isLoading = false
                })
            )
            .subscribe(response => {
                this.modalController.dismiss(response)
            })
    }

    public getCardBalance(code: string): string {
        const found = this.balances.find(b => b.curSymbol === code)
        if (found) {
            return found.balance + ''
        }
        return '0.00'
    }

    public useMax(): void {
        this.subcriptions.add(
            this.walletsQuery.balances$.subscribe(balances => {
                const found = balances.find(balance => balance.currency.code === this.from.code)
                if (found) {
                    this.topup.amount = parseFloat(found.balance)
                }
            })
        )
    }

    public calculateFee(): string {
        const { relativeFee, fixedFee } = this.feeService.determineFee(
            {
                amount: this.topup.amount + '' || '0',
                currency: this.currency,
            },
            this.cardTopupFee
        )
        const relativeFeeAmount = new BigNumber(this.topup.amount || 0).times(relativeFee || 0)
        const totalFee = new BigNumber(fixedFee || 0).plus(relativeFeeAmount)

        return totalFee.toFixed(2, 1)
    }

    public isForexTopup(): boolean {
        return this.from.code !== this.topup.destinationCurrencyCode
    }

    public getExchangeRate(): void {
        this.isGettingExchangeRate = true
        if (this.from.code !== this.topup.destinationCurrencyCode) {
            this.http
                .post<GetExchangeRateResponse>(
                    `/card-programs/${this.item.cardholder?.cardProgram?.id}/get-exchange-rate`,
                    {
                        sourceCurrencyCode: this.from.code,
                        destinationCurrencyCode: this.topup.destinationCurrencyCode,
                    }
                )
                .pipe(
                    finalize(() => {
                        this.isGettingExchangeRate = false
                    })
                )
                .subscribe(response => {
                    this.exchangeRate = response.exchangeRate
                })
        } else {
            this.exchangeRate = '1'
            this.isGettingExchangeRate = false
        }
    }

    public calculateAmount(): string {
        if (!this.topup.amount) {
            return '0'
        }

        return new BigNumber(this.topup.amount || 0)
            .minus(new BigNumber(this.calculateFee()))
            .times(this.exchangeRate)
            .toFixed(2)
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }
}
