import { HttpClient } from '@angular/common/http'
import { Component, ElementRef, OnDestroy, OnInit } from '@angular/core'
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { tap } from 'rxjs/operators'
import { ToastrService } from '../../../common/services/toastr.service'

const ease = require('ease-component')
const scroll = require('scroll')

interface Terms {
    name: string
    versionId: string
    description: string
}

@Component({
    selector: 'card-order-terms',
    templateUrl: 'card-order-terms.component.html',
})
export class CardOrderTermsComponent implements OnInit, OnDestroy {
    public terms: Terms
    public isDown = false
    public viewOnly = false
    public isLoaded = false

    private modalContainer: HTMLElement

    constructor(
        private element: ElementRef,
        private toastr: ToastrService,
        private http: HttpClient,
        public modal: NgbActiveModal,
        public translate: TranslateService
    ) {}

    public ngOnInit(): void {
        this.modalContainer = this.element.nativeElement.querySelector('.card-terms-container') as HTMLElement
        this.modalContainer.addEventListener('scroll', this.scrollHandler)

        this.http
            .get<Terms>('/nium/terms')
            .pipe(
                tap(terms => {
                    this.terms = terms
                    this.isLoaded = true
                    setTimeout(() => {
                        this.scrollHandler()
                    })
                })
            )
            .subscribe(() => undefined)
    }

    public ngOnDestroy(): void {
        this.modalContainer.removeEventListener('scroll', this.scrollHandler)
    }

    public scrollDown(): void {
        scroll.top(this.modalContainer, this.modalContainer.scrollHeight - this.modalContainer.clientHeight, {
            duration: 1000,
            ease: ease.inOutExpo,
        })
    }

    public accept(): void {
        if (!this.isDown) {
            this.toastr.warning(
                this.translate.instant('common.please-scroll-to-bottom'),
                this.translate.instant('common.terms')
            )
            return
        }
        this.modal.close({ ...this.terms, accept: true })
    }

    private scrollHandler = () => {
        // 25px threshold
        if (
            this.isLoaded &&
            !this.isDown &&
            this.modalContainer.scrollTop >= this.modalContainer.scrollHeight - this.modalContainer.offsetHeight - 25
        ) {
            setTimeout(() => {
                this.isDown = true
            })
        }
    }
}
