import { HttpClient } from '@angular/common/http'
import { Component, OnInit } from '@angular/core'
import { faCopy } from '@fortawesome/pro-light-svg-icons'
import { ModalController } from '@ionic/angular'
import { ICard } from '../../api-interfaces'

interface CardDetails {
    unMaskedCardNumber: string
    cvv: string
    expiry: string
}

@Component({
    selector: 'card-details',
    templateUrl: 'card-details.page.html',
})
export class CardDetailsPage implements OnInit {
    public item: ICard
    public details: CardDetails

    public faCopy = faCopy
    public backgroundImage: string

    constructor(private modalController: ModalController, private http: HttpClient) {}

    public ngOnInit(): void {
        this.http.get<CardDetails>(`/cards/${this.item.id}/details`).subscribe(details => (this.details = details))
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }

    public formatCardNumber(cardNumber: string): string {
        return `${cardNumber.slice(0, 4)} ${cardNumber.slice(4, 8)} ${cardNumber.slice(8, 12)} ${cardNumber.slice(12)}`
    }

    public getBackgroundImageStyle() {
        if (!this.backgroundImage) {
            return
        }
        const styles = {
            backgroundImage: 'url(' + this.backgroundImage + ')',
            backgroundSize: 'cover',
        }
        return styles
    }
}
