import { HttpClient } from '@angular/common/http'
import { Component, Input, OnDestroy, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { Router } from '@angular/router'
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap'
import { TranslateService } from '@ngx-translate/core'
import { pick } from 'lodash'
import { Subscription } from 'rxjs'
import { finalize } from 'rxjs/operators'
import { ICardOrder } from 'src/app/common/api-interfaces'
import { SecurityCheckService } from 'src/app/common/security-check/security-check.service'
import { SessionService } from 'src/app/common/services/session.service'
import { ToastrService } from 'src/app/common/services/toastr.service'
import { CardOrderTermsComponent } from '../card-order/card-order-terms/card-order-terms.component'

@Component({
    selector: 'card-order-confirmation',
    templateUrl: 'card-order-confirmation.component.html',
})
export class CardOrderConfirmationComponent implements OnDestroy {
    @ViewChild('cardOrderConfirmForm')
    public cardOrderConfirmForm: NgForm

    @Input()
    public cardOrder: ICardOrder

    public isLoading = false

    private subscriptrions = new Subscription()

    constructor(
        public activeModal: NgbActiveModal,
        private ngbModal: NgbModal,
        private http: HttpClient,
        private router: Router,
        private securityCheckService: SecurityCheckService,
        private toastr: ToastrService,
        public session: SessionService,
        public translate: TranslateService
    ) {}

    public ngOnDestroy(): void {
        this.subscriptrions.unsubscribe()
    }

    public submit(): void {
        if (!this.cardOrderConfirmForm.valid) {
            return
        }
        this.subscriptrions.add(
            this.securityCheckService.getScopePermission('banking').subscribe(() => {
                this.ngbModal
                    .open(CardOrderTermsComponent, {
                        windowClass: 'modal-primary',
                        backdrop: 'static',
                    })
                    .result.then(
                        terms => {
                            this.isLoading = true
                            this.http
                                .post<ICardOrder>('/card-orders', {
                                    id: this.cardOrder.id,
                                    type: this.cardOrder.type,
                                    currency: { code: this.cardOrder.currency.code },
                                    transaction: { currency: { code: this.cardOrder.currency.code } },
                                    user: { id: this.cardOrder.user.id },
                                    shippingAddress: this.cardOrder.shippingAddress,
                                    terms: pick(terms, ['name', 'accept', 'versionId']),
                                })
                                .pipe(
                                    finalize(() => {
                                        this.isLoading = false
                                    })
                                )
                                .subscribe(async () => {
                                    this.toastr.success(
                                        this.translate.instant('card-order-confirmation.card-order-confirmed')
                                    )
                                    this.activeModal.close()
                                    if (!this.router.url.includes('admin')) {
                                        await this.router.navigate(['/cards/orders'])
                                    }
                                })
                        },
                        () => undefined
                    )
            })
        )
    }
}
