import { Component } from '@angular/core'
import { faCalendarAlt } from '@fortawesome/pro-regular-svg-icons'
import { ModalController } from '@ionic/angular'
import { NgbCalendar, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap'
import { Subscription } from 'rxjs'
import { flatMap } from 'rxjs/operators'
import { ICard } from '../../api-interfaces'
import { SessionService } from '../../services/session.service'

@Component({
    selector: 'app-modal-download-transactions',
    templateUrl: './modal-download-transactions.page.html',
    styleUrls: ['../card-view-modal.scss'],
})
export class ModalDownloadTransactionsPage {
    public item: ICard
    public isLoading = false

    public faCalendarAlt = faCalendarAlt

    public fromDate: NgbDateStruct
    public toDate: NgbDateStruct
    private subscriptions = new Subscription()

    constructor(
        public session: SessionService,
        private modalController: ModalController,
        public calendar: NgbCalendar
    ) {
        this.fromDate = { year: new Date().getFullYear(), month: 1, day: 1 }
        this.toDate = this.calendar.getToday()
    }

    public downloadReport(): void {
        this.subscriptions.add(
            this.session.userStream
                .pipe(
                    flatMap(user =>
                        this.session.createSignedUrl(`/users/${user.id}/cards/${this.item.id}/transactions/report`)
                    )
                )
                .subscribe(signedUrl => {
                    window.open(
                        `${signedUrl}&from=${this.parseNgbdate(this.fromDate)}&to=${this.parseNgbdate(this.toDate)}`,
                        '_blank',
                        'noopener'
                    )
                    this.dismiss()
                })
        )

        this.session.createSignedUrl(`/cards/${this.item.id}/transactions/report`)
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }

    protected parseNgbdate(date: NgbDateStruct | null): string {
        return `${date?.year}-${date?.month.toLocaleString('en-US', {
            minimumIntegerDigits: 2,
            useGrouping: false,
        })}-${date?.day.toLocaleString('en-US', { minimumIntegerDigits: 2, useGrouping: false })}`
    }
}
