import { HttpClient } from '@angular/common/http'
import { Component, OnInit, ViewChild } from '@angular/core'
import { NgForm } from '@angular/forms'
import { ModalController } from '@ionic/angular'
import { TranslateService } from '@ngx-translate/core'
import { finalize } from 'rxjs/operators'
import { environment } from 'src/environments/environment'
import { ICard } from '../../api-interfaces'
import { SessionService } from '../../services/session.service'

interface LoadPayload {
    sourceCurrencyCode: string
    amount: number | string
    destinationCurrencyCode: string
}

interface UnloadPayload {
    currencyCode: string
    comments: string
    amount: string
}

interface NiumFundWalletResponse {
    systemReferenceNumber: string
    sourceAmount: number
    sourceCurrencyCode: string
    destinationAmount: number
    destinationCurrencyCode: string
    status: 'Pending' | 'Approved' | 'Declined'
    paymentMethods: string[]
}

interface NiumRefundWalletResponse {
    message: string
    systemReferenceNumber: string
}

@Component({
    selector: 'app-modal-offledger',
    templateUrl: './modal-offledger.page.html',
})
export class ModalOffledgerPage implements OnInit {
    public item: ICard
    public balances: any[]
    public type: string
    public apiUrl: string
    public body: LoadPayload | UnloadPayload

    public load: LoadPayload = {
        sourceCurrencyCode: 'USD',
        amount: '',
        destinationCurrencyCode: 'USD',
    }
    public unload: UnloadPayload = {
        currencyCode: 'USD',
        amount: '',
        comments: '',
    }
    public isLoading = false

    public niumCurrencies = ['AUD', 'CAD', 'EUR', 'GBP', 'HKD', 'JPY', 'SGD', 'USD']

    public appName = environment.appName

    @ViewChild('offLedgerForm')
    public offLedgerForm: NgForm

    constructor(
        private http: HttpClient,
        public session: SessionService,
        private modalController: ModalController,
        public translate: TranslateService
    ) {}

    ngOnInit() {}

    public async submitOffLedgerForm(): Promise<void> {
        if (this.offLedgerForm.invalid) {
            return
        }
        this.isLoading = true
        if (this.type === 'load') {
            this.load.amount += ''
            this.http
                .post<NiumRefundWalletResponse>(`/cards/${this.item.id}/load`, this.load)
                .pipe(
                    finalize(() => {
                        this.isLoading = false
                    })
                )
                .subscribe(response => {
                    this.modalController.dismiss(response)
                })
        } else {
            this.unload.amount += ''
            this.http
                .post<NiumFundWalletResponse>(`/cards/${this.item.id}/unload`, this.unload)
                .pipe(
                    finalize(() => {
                        this.isLoading = false
                    })
                )
                .subscribe(response => {
                    this.modalController.dismiss(response)
                })
        }
    }

    public dismiss(): void {
        this.modalController.dismiss()
    }
}
